










































































import Vue from "vue";
import LoadingErrorable from "@/models/util/LoadingErrorable";
import IJob from "@/models/jobs/IJob";
import JobProvider from "@/api/JobProvider";

import SpriteIcon from "@/components/util/SpriteIcon.vue";
import Loader from "@/components/util/Loader.vue";
import JobStatScaling from "@/components/job/JobStatScaling.vue";
import UiString from "@/components/uistring/UiString.vue";
import SkillTree from "@/components/skill/SkillTree.vue";
import Tab from "@/components/Tab/Tab.vue";
import Tabs from "@/components/Tab/Tabs.vue";
interface IData {
    job: LoadingErrorable<IJob, any>;
}

export default Vue.extend({
    components: {
        Loader,
        SpriteIcon,
        JobStatScaling,
        UiString,
        SkillTree,
        Tab,
        Tabs
    },
    props: {
        "jobId": {}
    },
    data(): IData {
        return {
            job: new LoadingErrorable(),
        };
    },
    watch: {
        jobId(newId, oldId) {
            this.fetchData();
        }
    },
    mounted() {
        this.fetchData();
    },
    methods: {
        fetchData() {
            this.job.startLoad();
            JobProvider.getJob(Number(this.jobId), true).then((job) => {
                this.job.done(job);
            }).catch((error: any) => {
                this.job.failed(error);
            });
        },
        getBreadcrumb() {
            let ret = [];
            let j = this.job.value ? this.job.value.parentJob : null;
            while (j) {
                ret.push(j);
                j = j.parentJob;
            }

            return ret.reverse();
        },
        getClassName(): string {
            if (this.job.value) {
                const t = this.job.value.classType;
                switch (t) {
                    case 0:
                        return "";
                    case 1:
                        return "黑暗";
                    case 2:
                        return "光明";
                    default:
                        return String(t);
                }
            }

            return "";
        },
        getClassType(): string {
            if (this.job.value) {
                const t = this.job.value.classType;
                switch (t) {
                    case 0:
                        return "";
                    case 1:
                        return "dark";
                    case 2:
                        return "light";
                    default:
                        return String(t);
                }
            }

            return "";
        },
        jobFamily(){
            let ret = [];
            let j = this.job.value ? this.job.value.parentJob : null;
            while (j) {
                ret.push({id:j.id,name:j.name.message});
                j = j.parentJob;
            }
            ret.reverse();

            let p = this.job.value ? this.job.value : null;
            if(p){
                ret.push({id:p.id,name:p.name.message})
            }
            return ret.reverse();
        },
    }

});
