<template lang="html">
  <div :class='{"tabs__v": mode === "verticle", "tabs__h": mode === "horizon"}'>
    <ul class='tabs__header'>
      <li v-for='(tab, index) in tabs'
        :key='tab.title'
        @click='selectTab(index,tab.id)'
        :class='{"tab__selected": (index == selectedIndex)}'>
        {{ tab.title }}
      </li>
    </ul>
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    mode: {
      type: String,
      default: 'horizon'
    },
    defaultTabId:{
       type: String,
       default: '0'
    }
  },
  data () {
    return {
      selectedIndex: 0, // the index of the selected tab,
      tabs: []         // all of the tabs
    }
  },
  created () {
    this.tabs = this.$children
  },
  mounted () {
    this.selectTab(0,parseInt(this.defaultTabId))
  },
  methods: {
    selectTab (i,tabId) {
      this.selectedIndex = i

      // loop over all the tabs
      this.tabs.forEach((tab, index) => {
        tab.isActive = (index === i)
      })

      this.$emit('clickTab',tabId);
    }
  }
}
</script>

<style lang="less">
@import "../../less/core.less";
  ul.tabs__header {
    display: block;
    list-style: none;
    margin: 0 0 0 20px;
    padding: 0;
  }

  ul.tabs__header > li {
    padding: 15px 30px;
    //border-radius: 10px;
    text-transform: uppercase;
    font-family: @dv-f-geomanist;
    border: 1px solid @dv-c-accent-1;
    text-align: center;
    background: fade(@dv-c-background, 40%);
    margin: 0;
    display: inline-block;
    margin-right: 5px;
    cursor: pointer;
    margin-bottom:5px;

    &:first-child {
        border-top-width: 1px;
    }

    &:hover {
        background: fade(@dv-c-foreground, 40%);
    }

    &[active] {
        border-color: @dv-c-accent-2;
        border-top-width: 1px;
        margin-top: -1px;
        background: fade(@dv-c-foreground, 10%);

        &:first-child {
            margin-top: 0;
        }
    }
  }

  ul.tabs__header > li.tab__selected {
    font-weight: bold;
    //border-radius: 10px 10px 0 0;
    //border-bottom: 8px solid transparent;
  }

  .tab-component {
    display: inline-block;
    //color: black;
    padding: 20px;
    min-width: 800px;
    border-radius: 10px;
    min-height: 400px;
  }

  .tabs__h .tab{
    //background-color: fade(@dv-c-background, 40%);
  }

  .tabs__h li.tab__selected {
    background-color: @dv-c-accent-1;
    color: white;
  }

  .tabs__v{
    display:flex;
    ul.tabs__header > li{
      min-width:70px;
    }
  }

  .tabs__v .tab{
    background-color: fade(@dv-c-background, 40%);
  }

  .tabs__v li.tab__selected {
    background-color: @dv-c-accent-1;
    color: white;
  }

</style>