<template lang="html">
  <div class='tab-component' v-if='isActive'>
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: 'Tab'
    },
    id: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      isActive: true
    }
  }
}
</script>
